<template>
  <div id="product" class="container product-container">
    <!--头部控件-->
    <common-header :options="headerOptions" :moreOpera="createMoreOpera()"></common-header>

    <div class="content">
      <!--查询控件-->
      <product-query ref="product-query" :options="selected" @product-query-change="PruductQueryChange"></product-query>

      <div class="line-body" ref="lineBody"
           v-infinite-scroll="loadMore"
           infinite-scroll-immediate-check="false"
           infinite-scroll-disabled="loading"
           infinite-scroll-distance="10">
        <div class="line-row" v-for="(item, index) in list">
          <!--每一行数据的控件-->
          <line-row :item="item" :index="index"></line-row>
        </div>
        <div v-if="list.length == 0 && !loading" class="load-tip">
          <div class="inline-block">没有符合条件的数据</div>
        </div>
        <div v-if="loading" class="load-tip">
          <img src="../../assets/image/loading.gif" alt="">
          <div class="inline-block">正在加载...</div>
        </div>
      </div>

      <div class="create-button">
        <create></create>
      </div>

    </div>
  </div>
</template>
<script>
  import Vue from 'vue'
  import CommonHeader from '../common/CommonHeader.vue'
  import LineRow from './LineRow.vue'
  import ProductQuery from './ProductQuery.vue'
  import Create from './PaletteButton.vue'

  export default {
    name: 'Product',
    components: {
      CommonHeader, LineRow, ProductQuery, Create
    },
    data() {
      return {
        headerOptions: {  // 顶部显示的文字和位置
          title: '媒资库',
          align: 'center'
        },

        list: [], // 列表数据
        loading: false, // 正在加载的标识

        start: 0, // 分页相关
        limit: 10,
        totalCount: 0,
        currentPage: 1,
        totalPage: 1,

        orderBy: 'created desc',
        content: '',
        statusName: '',
        beginTime : '',
        endTime : '',
        organizationId : '',
        selected : '1' // 1--全部，2--我的
      }
    },
    mounted() {
      this.search();
      this.$refs.lineBody.style.height = document.documentElement.clientHeight - 115 + 'px';
      var that = this;
      /*绑定resize事件*/
      window.onresize = function () {
        if(that.$refs.lineBody)
          that.$refs.lineBody.style.height = document.documentElement.clientHeight - 115 + 'px';
      }
    },
    methods: {
      createMoreOpera: function () {
        var moreOpera = {};
        moreOpera.visiable = true;
        moreOpera.operas = [];
        var opera1 = {};
        opera1.title = "确定";
        opera1.icon = '';
        opera1.method = this.commit;
        moreOpera.operas.push(opera1);
        var opera2 = {};
        opera2.title = "取消";
        opera2.icon = '';
        opera2.method = this.commit;
        moreOpera.operas.push(opera2);
        return moreOpera;
      },
      loadMore: function () {
        if (this.currentPage < this.totalPage) {
          this.currentPage = this.currentPage + 1;
          this.search();
        }
      },
      searchData: function (param) {
        let vm = this;
        this.loading = true;
        let userId = vm.$global.userId;
        vm.$http.post(param.url, qs.stringify({para: JSON.stringify(param), userId: userId}))
          .then(function (res) {
            vm.list = vm.list.concat(res.data.items);
            vm.currentPage = res.data.currentPage;
            vm.totalPage = res.data.totalPage;
            vm.loading = false;
          })
          .catch(function (err) {
            console.log("error:" + err.status);
            vm.loading = false;
          })
      },
      PruductQueryChange : function(val,tabObj){
        this.selected = val;
        this.statusName = tabObj.status.id;
        if(tabObj.time.id){
          this.beginTime = tabObj.time.id.split(',')[0];
          this.endTime = tabObj.time.id.split(',')[1];
        }else{
          this.beginTime = '';
          this.endTime = '';
        }
        this.organizationId = tabObj.organization.id;
        this.refresh();
      },
      refresh: function () {
        this.list = [];
        this.currentPage = 1;
        this.search();
      },
      search: function () {
        let param = {};
        param.url = this.$global.getUrlByAppId("RESOURCEMANAGER") + "/basicsearch";
        param.start = (this.currentPage - 1) * this.limit;
        param.limit = this.limit;
        param.orderBy = this.orderBy;
        param.conditions = this.getSearchConditions();
        param.extendResultFields = "作者,机构ID,机构名称,产品状态,创作时间,签发资源ID,审核人ID";
        if (this.content) {
          param.url = this.$global.getUrlByAppId("RESOURCEMANAGER") + "/search";
        }
        console.log(param);
        this.searchData(param);
      },
      getSearchConditions: function () {
        let conditions = [];

        //基本的默认条件，非文件夹类
        conditions.push({id: "type", value: "7", operator: '1'});
        conditions.push({id: 'CREATORID', value: 'e18a0f771ac4485fa9107d8382326e57'});
        if (this.statusName)
          conditions.push({id: '产品状态', value: this.statusName});

        conditions.push({"id": "FOLDERID", "value": "PRODUCT_ROOT_FOLDERID"});

        if(this.organizationId)
          conditions.push({"id": "机构ID", "value": this.organizationId});

        if (this.content)
          conditions.push({id: 'CONTENT', value: this.content});

        if(this.selected === '2')
          conditions.push({id: '审核人ID', value: 'e18a0f771ac4485fa9107d8382326e57'});

        if (this.beginTime && this.endTime) {
            conditions.push({
                id: 'CREATED',
                value: this.beginTime + "," + this.endTime,
                dataType: 3,
                operator: 2
            });
        }
        return conditions;
      },
      commit : function () {

      }
    }
  }
</script>
<style lang="scss">
.product-container{
  .line-body {
    overflow: auto;
  }

  .line-row {
    border-bottom: 1px solid #dedede;
    line-height: 30px;
  }

  .load-tip {
    text-align: center;
    line-height: 30px;
    margin-top: 10px;
    color: #000;
    font-size: 14px;
  }

  .load-tip img {
    width: 20px;
    height:20px;
    vertical-align: middle;
  }
  .query-popup {
    width: 100%;
    height: 80%;
  }
  .create-button {
    position: fixed;
    font-size:16px;
    left: -20px;
    bottom: 10px;
  }
}
</style>



