<template>
  <div>
    <div style="margin-top: 90px">
      <div id="head" style="display: none">
        <mt-field placeholder="引题" v-model="head"></mt-field>
      </div>

      <div style="margin-bottom: 50px; height: auto">
        <div style="float: left">
          <v-touch v-on:press="show">
            <mt-field placeholder="请输入标题" v-model="headline"></mt-field>
          </v-touch>
        </div>
        <div>
          <mt-popup
            :modal=false
            v-model="popupVisible"
            popup-transition="popup-fade">
            <mt-cell @click.native="showhead" title="添加引题"></mt-cell>
            <mt-cell @click.native="showsubhead" title="添加副标题"></mt-cell>
          </mt-popup>
        </div>
      </div>

      <div id="subhead" style="display: none">
        <mt-field placeholder="副标题" v-model="subhead"></mt-field>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        head: '',
        headline: '',
        subhead: '',
        popupVisible: false
      }
    },
    methods: {
      show: function () {
        if ((document.getElementById('head').style.display === 'block') && (document.getElementById('subhead').style.display === 'block')) {
          return false;
        }
        this.popupVisible = true;
        return false;
      },

      showhead: function () {
        console.log(document.getElementById('head'))
        document.getElementById('head').style.display = 'block'
        this.popupVisible = false
      },
      showsubhead: function () {
        document.getElementById('subhead').style.display = 'block'
        this.popupVisible = false
      }
    }
  }
</script>

<style>

</style>



