<template>
  <div>
    <common-header :options="options"></common-header>
    <div style="margin-top: 90px">
      <head-line></head-line>
      <hr>
      <span class="tip">已输入{{ content.length}}个字</span>
      <!--<mt-field placeholder="正文" type="textarea" rows="10" v-model="fulltext"></mt-field>-->

      <editor @change="updateData" :content="content" :height="500" :z-index="1000" :auto-height="true" :show-module-name="false"></editor>

    </div>

    <foot-button v-bind:save="textSave" v-bind:commit="textCommit" class="btndiv"></foot-button>
  </div>


</template>

<script>
  import CommonHeader from '@/components/common/CommonHeader'
  import FootButton from '@/components/create/Button'
  import HeadLine from '@/components/create/HeadLine'


  import VueHtml5Editor from 'vue-html5-editor'

  var options = {
    name: "vue-html5-editor",
    // 是否显示模块名称，开启的话会在工具栏的图标后台直接显示名称
    // if set true,will append module name to toolbar after icon
    showModuleName: false,
    // 自定义各个图标的class，默认使用的是font-awesome提供的图标
    // custom icon class of built-in modules,default using font-awesome
    icons: {
      text: "fa fa-pencil",
      color: "fa fa-paint-brush",
      font: "fa fa-font",
      align: "fa fa-align-justify",
      list: "fa fa-list",
      link: "fa fa-chain",
      unlink: "fa fa-chain-broken",
      tabulation: "fa fa-table",
      image: "fa fa-file-image-o",
      hr: "fa fa-minus",
      eraser: "fa fa-eraser",
      undo: "fa-undo fa",
      "full-screen": "fa fa-arrows-alt",
      info: "fa fa-info",
    },
    // 配置图片模块
    // config image module
    image: {
      // 文件最大体积，单位字节  max file size
      sizeLimit: 512 * 1024,
      // 上传参数,默认把图片转为base64而不上传
      // upload config,default null and convert image to base64
      upload: {
        url: null,
        headers: {},
        params: {},
        fieldName: {}
      },
      // 压缩参数,默认使用localResizeIMG进行压缩,设置为null禁止压缩
      // compression config,default resize image by localResizeIMG (https://github.com/think2011/localResizeIMG)
      // set null to disable compression
      compress: {
        width: 1600,
        height: 1600,
        quality: 80
      },
      // 响应数据处理,最终返回图片链接
      // handle response data，return image url
      uploadHandler(responseText){
        //default accept json data like  {ok:false,msg:"unexpected"} or {ok:true,data:"image url"}
        var json = JSON.parse(responseText)
        if (!json.ok) {
          alert(json.msg)
        } else {
          return json.data
        }
      }
    },
    language: "zh-cn",
    // 隐藏不想要显示出来的模块
    hiddenModules: [],
    // 自定义要显示的模块，并控制顺序
    visibleModules: [
      "text",
      "color",
      "font",
      "align",
      "list",
      "link",
      "unlink",
      "tabulation",
      "image",
      "hr",
      "eraser",
      "undo",
      "full-screen",
    ],
  }
  const editor = new VueHtml5Editor(options)

  export default {
    components: {CommonHeader, FootButton, HeadLine, editor},
    data () {
      return {
        options: {
          title: '新建文本',
          align: 'center'
        },
        fulltext: '',
        content: ''
      }
    },
    methods: {
      textSave: function () {
        this.$http.put('http://192.168.8.196:8090/cre/api/resources/richText/cac99535-f2aa-43bf-a4ed-b82e04fe9819?newVersion=fasle', JSON.stringify({
          attachmentList: [],
          auditorName: '',
          authorName: '黄少明',
          content: '<p>这是移动端新建文字<br/></p>',
          contentText: '这是移动端新建文字↵',
          editorId: 'e18a0f771ac4485fa9107d8382326e57',
          eyebrowHead: '移动端',
          folderId: 'PRODUCT_ROOT_FOLDERID',
          keyWords: '',
          productStatus: '草稿',
          source: '',
          subtitle: '移动端',
          title: '移动端',
          topicName: ''
        })).then(function (response) {
          console.log(response)
        })
      },
      textCommit: function () {
        alert('bbbb')
        console.log(this.$refs.editor.$txt.html())
      },

      updateData: function(data) {
        console.log(data)
        this.content = data.trim()
      }
    }
  }
</script>

<style>
  .tip {
    padding-left: 70%;
    text-align: right;
    font-size: xx-small;
  }

  .btndiv {
    position: fixed;
    bottom: 10%;
    align-content: center
  }





</style>



