<template>
  <div class="select2">
    <div v-for="item in options.data" :class="['options',initSelect(item.id)?'active':'']" @click="onSelect(item.id)">
      {{item.name}}
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {}
    },
    props: ['options'],
    methods: {
      initSelect: function (value) {
        return this.options.selected === value;
      },
      onSelect: function (value) {
        this.options.callback(value);
      }
    }
  }
</script>
<style>
  .select2 .options {
    text-align: left;
    height: 30px;
    line-height: 30px;
    padding: 5px 5px 5px 25px;
    border-bottom: 1px solid #dedede;
  }

  .select2 .active {
    background-color: #ef4f4f;
    color: #ffffff;
  }
</style>



