import cookie from './cookie'

export default function () {
  return {
    userId: cookie().getCookie("userId"),

    getUrlByAppId: function (appId) {
      return cookie().getCookie(appId);
    },

    getUserId: function () {
      return userId;
    },

    //时间格式化方法
    dateFormat: function (date, fmt) {
      var o = {
        "M+": date.getMonth() + 1,                 //月份
        "d+": date.getDate(),                    //日
        "h+": date.getHours(),                   //小时
        "m+": date.getMinutes(),                 //分
        "s+": date.getSeconds(),                 //秒
        "q+": Math.floor((date.getMonth() + 3) / 3), //季度
        "S": date.getMilliseconds()             //毫秒
      };
      if (/(y+)/.test(fmt))
        fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
      for (var k in o)
        if (new RegExp("(" + k + ")").test(fmt))
          fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
      return fmt;
    },
    arrayContains: function (array, item, attr) {
      var result = false;
      for (var i = 0; i < array.length; i++) {

        var thisItem = attr ? array[i][attr] : array[i];
        var thatItem = attr ? item[attr] : item;

        if (thisItem == thatItem) {
          result = true;
          break;
        }
      }
      return result;
    },
    arrayDelete: function (array, item, attr) {
      var n;
      for (var i = 0; i < array.length; i++) {

        var thisItem = attr ? array[i][attr] : array[i];
        var thatItem = attr ? item[attr] : item;

        if (thisItem == thatItem) {
          n = i;
          break;
        }
      }
      if (n)
        array.splice(n, 1);
    },
    underline2Camel: function (str) {
      if (!str) return null;
      str = str.toLowerCase();
      var re = /_(\w)/g;
      return str.replace(re, function ($0, $1) {
        return $1.toUpperCase();
      });
    },
    deepCopy: function (source) {
      var clone = function (obj) {
        var o;
        switch (typeof obj) {
          case 'undefined':
            break;
          case 'string'   :
            o = obj + '';
            break;
          case 'number'   :
            o = obj - 0;
            break;
          case 'boolean'  :
            o = obj;
            break;
          case 'object'   :
            if (obj === null) {
              o = null;
            } else {
              if (obj instanceof Array) {
                o = [];
                for (var i = 0, len = obj.length; i < len; i++) {
                  o.push(clone(obj[i]));
                }
              } else {
                o = {};
                for (var k in obj) {
                  o[k] = clone(obj[k]);
                }
              }
            }
            break;
          default:
            o = obj;
            break;
        }
        return o;
      }
      return clone(source);
    }
  }
}



