<template>
  <div class="line-cell">
    <div class="line-cell-top">
      <img class="pull-left" src="../../assets/image/100x100.png" alt="">
      <img class="pull-right row-menu" src="../../assets/image/menu.png" alt="" @click="toggleButtons()">
      <div :class="{ browsed: item.browse }">{{item.originalName}}</div>
    </div>
    <div class="line-cell-mid">
      <div class="inline-block element-time">{{dateFormat(item.created)}}</div>
      <div class="inline-block element-name">{{item.creatorName}}</div>
      <div v-for="attributes in item.extendAttributes" v-if="attributes.id == '机构名称'" class="inline-block element-dept">
        {{getLeafOrgName(attributes.value)}}
      </div>
      <div v-show="false" v-for="attributes in item.extendAttributes" v-if="attributes.id == '产品状态'">
        {{getStatus(attributes.value)}}
      </div>
      <div v-show="false" v-for="attributes in item.extendAttributes" v-if="attributes.id == '机构ID'">
        {{getOrganizationId(attributes.value)}}
      </div>
    </div>
    <div v-show="operator" class="line-cell-bottom">
      <mt-button v-for="button in buttonList" :key="button.privilegeId" :id="button.privilegeId"
                 :type="button.color?'danger':'default'" size="small" @click="buttonMethod(button.privilegeId)">
        {{button.aliasName}}
      </mt-button>
    </div>

  </div>
</template>
<script>
  import {MessageBox, Toast} from 'mint-ui';

  export default {
    components: {
      MessageBox, Toast
    },
    data() {
      return {
        operator: false,
        buttonList: null,
        organizationId: '',
        status: ''
      }
    },
    props: ['item', 'index'],
    mounted() {

    },
    methods: {
      toggleButtons: function () {
        let vm = this;
        let userId = vm.$global.userId;
        let url = vm.$global.getUrlByAppId("CRE") + "/api/authorization/privilege/loadbuttons";
        if (!this.buttonList) {
          vm.$http.put(url, JSON.stringify({
            "userId": userId,
            "organizationId": vm.organizationId,
            'status': vm.status
          }))
            .then(function (res) {
              vm.buttonList = res.data.privileges || [];
              vm.buttonList = vm.sortPrivilegeButtons(vm.buttonList);
              vm.operator = !vm.operator;
            })
            .catch(function (err) {
              console.log("error:" + err.status);
            })
        } else {
          vm.operator = !vm.operator;
        }

      },
      delete: function () {

      },
      dateFormat: function (value) {
        return this.$global.dateFormat(new Date(value), "yyyy/MM/dd hh:mm");
      },
      getLeafOrgName: function (orgName) {
        var index = orgName.lastIndexOf('-');
        if (index > 0) {
          return orgName.substring(index + 1);
        } else {
          return orgName;
        }
      },
      getStatus: function (value) {
        this.status = value;
        return value;
      },
      getOrganizationId: function (value) {
        this.organizationId = value;
        return value;
      },
      sortPrivilegeButtons: function (privilegeButtons) {
        var sortPrivileges = [];
        /*for (var i = 0; i < privilegeButtons.length; i++) {
          if (privilegeButtons[i].privilegeId.indexOf("TO") > -1) {
            //privilegeButtons[i].color = true;
            //sortPrivileges.push(privilegeButtons[i]);
          }
        }*/
        for (var i = 0; i < privilegeButtons.length; i++) {
          if (!(privilegeButtons[i].privilegeId.indexOf("TO") > -1)) {
            sortPrivileges.push(privilegeButtons[i]);
          }
        }
        return sortPrivileges;
      },
      messageAlert: function (message) {
        Toast({
          message: message
        });
      },
      buttonMethod: function (privilegeId) {
        var methodName = this.$global.underline2Camel(privilegeId);
        if (this[methodName])
          this[methodName]();
      },
      //删除
      pidProductStatusDelete: function () {
        let vm = this;
        let userId = vm.$global.userId;
        let url = vm.$global.getUrlByAppId("RESOURCEMANAGER") + "/removeResourceToRecyclebin";
        var simpleObjectItems = [];
        var simpleObjectItem = {};
        simpleObjectItem.id = vm.item.id;
        simpleObjectItem.inPoint = 0;
        simpleObjectItem.outPoint = 0;
        simpleObjectItems.push(simpleObjectItem);
        var param = qs.stringify({
          objects: JSON.stringify(simpleObjectItems),
          owner: userId,
          sourceApp: "resourcemanager"
        });
        MessageBox.confirm('确定删除数据吗?').then(action => {
          vm.$http.post(url, param)
            .then(function (res) {
              vm.messageAlert('操作成功');
            })
            .catch(function (err) {
              console.log("error:" + err.status);
            })

        }).catch(err => {

        });
      },
      pidProductStatusDownload : function(){
        var url = this.$global.getUrlByAppId("RESOURCEMANAGER") + '/resource/' + this.item.id + '/downloadfiles';
        window.open(url);
      },
      pidProductCgAudit : function(){
        //router.push({name : 'audit4draft',params : {id : this.item.id,name:'333',resource:{id:'222'}}});
        router.push('audit4draft/' + this.item.id);
      }
    }
  }
</script>
<style lang="scss">
.line-cell{
  .browsed {
    color: #888888 !important;
  }

  .line-cell-top {
    padding: 5px 10px 5px 10px;
  }

  .line-cell-top div {
    font-size: 16px;
    font-weight: bold;
    color: #333333;
    min-height: 50px;
    margin-left: 60px;
    overflow: hidden;
    white-space: nowrap;
    word-break: break-all;
    text-overflow: ellipsis;
  }

  .line-cell-top img:first-child {
    width: 50px;
    height: 50px;
    margin-top: 3px;
  }

  .line-cell-top .row-menu {
    margin-top: 20px;
    margin-right: -10px;
    width: 32px;
    height: 32px;
    border-radius: 5px;
  }

  .line-cell-mid {
    margin-left: 70px;
    font-size: 14px;
  }

  .line-cell-mid div {
    color: #666666;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .line-cell-mid .element-time {
    width: 40%;
    text-align: left;
  }

  .line-cell-mid .element-name {
    width: 20%;
    text-align: center;
  }

  .line-cell-mid .element-dept {
    width: 35%;
    text-align: center;
  }

  .line-cell-bottom {
    font-size:14px;
    background-color: #FFF0F0;
    padding: 3px;
    text-align: center;
  }

  .line-cell-bottom .mint-button {
    margin: 3px;
  }
}
</style>



